.slide {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    /*position: relative;*/
    background: linear-gradient(to top, rgba(180, 180, 160, 0.4) 0%, rgba(180, 180, 160, 0.4) 100%),
    url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center center/cover;
}

.tabs {
    padding: 5em 0 5em;
    min-height: 60svh;
}

html {
    scroll-behavior: smooth; /* 自然なスムーススクロール */
}

.tabs-container {
    display: flex;
    position: fixed;
    bottom: 40px; /* 表示したいY位置（調整可能） */
    left: -100%;  /* 初期位置：画面外 */
    transform: translateX(0%); /* 初期位置用 */
    padding: 0.5rem 1rem 0;
    opacity: 0;
    mix-blend-mode: difference;
    /* アニメーション用 */
    animation: slideFadeIn 1s ease-out forwards;
    animation-delay: 1s; /* 遅延させたい場合は調整可能 */
    z-index: 100;
    transition: none;
}
.tabs-container.animate-in {
    animation: slideFadeIn 1s ease-out forwards;
}

/* アニメーション定義 */
@keyframes slideFadeIn {
    0% {
        left: -100%;
        opacity: 0;
    }
    100% {
        left: 0%;
        transform: translateX(0%);
        opacity: 1;
    }
}


.tabs-container--top {
    bottom: 1em;
    opacity: 1;
}

.tab {
    display: flex;
    align-items: center;
    letter-spacing: 0.1rem;
    transition: all 0.5s ease;
    text-decoration: none;
    position: relative;
    box-shadow: 0px 0px 8px 0px #000;
    backdrop-filter: blur(5px);
    background: rgba(46, 46, 46, 0.65);
    transition: bottom 0.3s ease, opacity 0.3s ease;
    z-index: 10;
}

.tabs-container a {
    position: relative;
    right: 0;
    align-items: center;
}
.tabs-container a:hover {
    color: var(--accent);
    background: var(--text);
    transition: all 0.35s ease;
}
.tabs-container a::after,
.tabs-container a::before {
    display: none;
}

.tab-slider {
    position: absolute;
    bottom: 0;
    height: 2px;
    background-color: var(--border);
    transition: left 0.25s ease, width 0.3s ease;
    z-index: 1;
}

@media screen and (max-width: 600px) {
    .tabs-container {
        display: grid;
        bottom: 5em;
        width: 35%;
        padding: 0;
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter: blur(5px);
        box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.75);
    }

    .tab {
        font-size: 1.4rem;
        transition: all 0.3s ease;
        box-shadow: 0 0px 0px 2.5px rgba(46, 46, 46, 0.35);
        -webkit-backdrop-filter: blur(5px);
        backdrop-filter: blur(5px);
        background: rgba(150, 150, 150, 0.85);
    }

    .tab:active {
        color: var(--text);
        background: var(--bg);
        transition: all 0.5s ease;
    }
    
    .tabs-container a {
        font-family: "Jost", serif;
        color: var(--text);
        display: inline;
        line-height: 2.4;
        text-shadow: 0px 0px 2px var(--text);
        padding-bottom: 0.25rem;
        margin: 0.5em 0.5em 0.5em 0;
        align-content: center;
    }
    
    .tabs-container a::after,
    .tabs-container a::before {
        display: none;
    }
    
    .tab-slider {
        border-radius: 3px;
        background-color: var(--bg);
    }
}`, "",{"version":3,"sources":["webpack://./src/style/tab.css"],"names":[],"mappings":"AAAA;;IAEI,aAAa;IACb,sBAAsB;IACtB,2BAA2B;IAC3B,sBAAsB;IACtB;yEACoE;AACxE;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,uBAAuB,EAAE,iBAAiB;AAC9C;;AAEA;IACI,aAAa;IACb,eAAe;IACf,YAAY,EAAE,mBAAmB;IACjC,WAAW,GAAG,aAAa;IAC3B,yBAAyB,EAAE,UAAU;IACrC,sBAAsB;IACtB,UAAU;IACV,0BAA0B;IAC1B,aAAa;IACb,2CAA2C;IAC3C,mBAAmB,EAAE,kBAAkB;IACvC,YAAY;IACZ,gBAAgB;AACpB;AACA;IACI,2CAA2C;AAC/C;;AAEA,cAAc;AACd;IACI;QACI,WAAW;QACX,UAAU;IACd;IACA;QACI,QAAQ;QACR,yBAAyB;QACzB,UAAU;IACd;AACJ;;;AAGA;IACI,WAAW;IACX,UAAU;AACd;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,sBAAsB;IACtB,yBAAyB;IACzB,qBAAqB;IACrB,kBAAkB;IAClB,gCAAgC;IAChC,0BAA0B;IAC1B,kCAAkC;IAClC,+CAA+C;IAC/C,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,mBAAmB;AACvB;AACA;IACI,oBAAoB;IACpB,uBAAuB;IACvB,0BAA0B;AAC9B;AACA;;IAEI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,+BAA+B;IAC/B,4CAA4C;IAC5C,UAAU;AACd;;AAEA;IACI;QACI,aAAa;QACb,WAAW;QACX,UAAU;QACV,UAAU;QACV,kCAAkC;QAClC,0BAA0B;QAC1B,2CAA2C;IAC/C;;IAEA;QACI,iBAAiB;QACjB,yBAAyB;QACzB,kDAAkD;QAClD,kCAAkC;QAClC,0BAA0B;QAC1B,qCAAqC;IACzC;;IAEA;QACI,kBAAkB;QAClB,qBAAqB;QACrB,yBAAyB;IAC7B;;IAEA;QACI,0BAA0B;QAC1B,kBAAkB;QAClB,eAAe;QACf,gBAAgB;QAChB,oCAAoC;QACpC,uBAAuB;QACvB,2BAA2B;QAC3B,qBAAqB;IACzB;;IAEA;;QAEI,aAAa;IACjB;;IAEA;QACI,kBAAkB;QAClB,2BAA2B;IAC/B;AACJ","sourcesContent":[".tabs,\r\n.slide {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: flex-start;\r\n    /*position: relative;*/\r\n    background: linear-gradient(to top, rgba(180, 180, 160, 0.4) 0%, rgba(180, 180, 160, 0.4) 100%),\r\n    url(\"../assets/images/pd-body-bg.jpg\") no-repeat center center/cover;\r\n}\r\n\r\n.tabs {\r\n    padding: 5em 0 5em;\r\n    min-height: 60svh;\r\n}\r\n\r\nhtml {\r\n    scroll-behavior: smooth; /* 自然なスムーススクロール */\r\n}\r\n\r\n.tabs-container {\r\n    display: flex;\r\n    position: fixed;\r\n    bottom: 40px; /* 表示したいY位置（調整可能） */\r\n    left: -100%;  /* 初期位置：画面外 */\r\n    transform: